<template>
  <div @click="toggleSwitch" :class="{
    'np-outer-switch': true,
    'np-outer-switch-on': isEnabled,
    'np-outer-switch-off': !isEnabled,
  }">
    <div :class="{
      'np-inner-switch': true,
      'np-inner-switch-left': !isEnabled,
      'np-inner-switch-right': isEnabled,
    }"></div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  props: ['val', 'toggle'],
  setup (props) {
    const isEnabled = ref(props.val)

    const toggleSwitch = () => {
      isEnabled.value = !isEnabled.value

      if (props.toggle) {
        props.toggle()
      }
    }

    return {
      isEnabled,
      toggleSwitch
    }
  }
}
</script>

<style lang="scss">

.np-outer-switch {
  width: 50px;
  height: 24px;
  border-radius: 30px;
  background: rgb(155, 155, 155);
  cursor: pointer;
  position: relative;
}

.np-outer-switch-off {
  background: rgb(141, 141, 141);
  transition: background 0.4s;
}

.np-outer-switch-on {
  background: #009300;
  transition: background 0.4s;
}

.np-inner-switch {
  height: 24px;
  width: 24px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  left: 0px;
}

.np-inner-switch-left {
  top: 0px;
  left: 0px;
  transition: left 0.4s;
}

.np-inner-switch-right {
  top: 0px;
  left: 26px;
  transition: left 0.4s;
}
</style>
