<template>
  <div class="channels-grid">
    <div class="categories">
      <div v-for="category in categories" :key="category.name" class="category">
        <h5 class="category-title">
          {{ category[locale] }}
        </h5>
        <div class="channels-grid-box">
          <div v-for="channel in category.channels" :key="channel.telegramName || channel.channelNickname"
            class="channel" @click="toggleActive(channel.telegramName || channel.channelNickname)">
            <div class="left-box">
              <div class="channel-icon">
                <img :src="channel.photoImage" loading="lazy" alt="">
                <div class="bg-placeholder">
                  <p style="text-align: center;">
                    {{
                      channel.channelName ? channel.channelName.slice(0, 1) : '?'
                    }}
                  </p>
                </div>

                <div v-if="channel.active" class="active">
                  <img :src="activeImg" alt="activeState">
                </div>
              </div>
              <div class="channel-name" v-html="channel.channelName"></div>
            </div>
            <div class="right-box">
              <toggle-button :val="channel.active" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useI18n } from 'vue-i18n'
import { ChannelService } from '@/services'
import { activeImg } from '@/files'
import ToggleButton from '@/components/ToggleButton'

export default {
  components: {
    ToggleButton,
  },
  setup() {
    const categories = ref(ChannelService.categories)
    const channels = ref(ChannelService.getStorageChannels())
    const { locale } = useI18n({ useScope: 'global' });

    categories.value = categories.value.map((category) => {
      return {
        ...category,
        channels: Object.values(channels.value || [])
          .filter((channel) => channel.category === category.name,)
          .sort((a, b) => a.channelNickname < b.channelNickname ? -1 : 1),
      }
    })

    categories.value = categories.value.filter((category) => category.name !== 'all')

    const toggleActive = async (channelNickname) => {
      const active = !channels.value[channelNickname].active

      ChannelService.updateChannel(channelNickname, { active })
      channels.value[channelNickname].active = active
    }

    return {
      channels,
      categories,
      activeImg,
      locale,
      toggleActive,
    }
  },
}
</script>

