<template>
  <div v-if="!loading" class="settings">
    <div class="">
      <h5 class="setting-title">{{ $t('settings') }}</h5>
      <div class="setting-item">
        <div class="setting-label">
          <p>{{ $t('night_mode') }}</p>
        </div>
        <toggle-button
          class="dark-mode-toggle-btn"
          :val="darkMode"
          :toggle="toggleDarkMode"
        />
      </div>
      <div class="setting-item">
        <div class="setting-label">
          <p>{{ $t('language') }}</p>
        </div>
        <q-select
          v-model="locale"
          :options="localeOptions"
          label=""
          dense
          borderless
          emit-value
          map-options
          options-dense
          class="setting-select"
          style="min-width: 150px; font-size: 18px"
        />
      </div>
      <h5 class="setting-title">{{ $t("channels_controls") }}</h5>
    </div>
    <channels-grid />
  </div>
  <div class="settings-loader" v-else>
    <img src="@/assets/img/loader.gif" alt="" />
  </div>
</template>
<script>
import ChannelsGrid from '@/components/ChannelsGrid.vue';
import ToggleButton from '@/components/ToggleButton.vue';
import { DarkModeService, PreloaderService } from '@/services';
import { onUnmounted, watch } from 'vue';
import { useStore } from 'vuex';
import { useI18n } from 'vue-i18n';
import { QSelect } from 'quasar';

export default {
  name: 'SettingsPage',
  components: {
    ChannelsGrid,
    ToggleButton,
    QSelect,
  },
  setup() {
    const store = useStore();
    const preloaderService = PreloaderService();
    const { locale } = useI18n({ useScope: 'global' });

    watch(
      () => locale.value,
      (newLocale) => {
        localStorage.setItem('locale', newLocale);
      },
    );

    onUnmounted(async () => {
      store.commit('post/reset');
    });

    return {
      loading: preloaderService.loading,
      darkMode: DarkModeService.darkMode,
      toggleDarkMode: DarkModeService.toggleDarkMode,
      locale,
      localeOptions: [
        { value: 'en', label: 'English' },
        { value: 'uk', label: 'Українська' },
        { value: 'ru', label: 'Русский' },
      ],
    };
  },
};
</script>

